










import { Component, Vue, Prop } from "vue-property-decorator";
import { Task } from "@/types";
import { format } from 'date-fns'
@Component({
  filters: {
  dateLocalize(date: Date) {
    return format(date, "yyyy-MM-dd")
  }
  }
})
export default class TaskList extends Vue {
    @Prop()
    public title!: string;
    @Prop()
    public tasks!: Task[];
}
